body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  text-align: center;
}

.toolswrapper {
  display: block;
  margin: 0 auto 24px; 
  max-width: 1230px;
}

.tools-container {
  display: flex;
  margin: 0 20px 20px;
}

.tools {
  justify-content: space-around;
}

.tool {
  margin: 0 10px;
}

.tools-label {
  margin-right: 5px;
}

.options {
  margin: 0 20px 20px;
}

.active {
  font-weight: bold;
}

.sketchpad-wrapper {
  margin: 0 10px;
}

.sketchpad-container {
  position: relative;
  max-width: 1230px;
  margin: 0 auto;
}

.canvas {
  border: 1px solid;
}

#canvas {
  width: 100%;
  height: 600px;
}

.eraser {
  cursor: crosshair;
}

